:root {
  --font-black: #000;
  --font-dark: #232323;
  --font-gray: #464646;
  --card-beige: #FAF0EA;
  --foter: #F6E7DA;
}

* {
  box-sizing: border-box;
  font-size: 16px;
}

body {
  font-family: "Montserrat", sans-serif;
  margin: 0 auto;
}

a {
  color: var(--font-black);
  transition: all 0.2s ease-in;
}
a:hover {
  color: var(--font-gray);
}

.container {
  max-width: 1600px;
  padding: 0 200px;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 114px;
  padding-top: 10px;
  margin: auto;
}
.header a {
  text-decoration: none;
}

.header-right {
  display: flex;
  align-items: center;
}

.header-menu {
  display: flex;
  margin-right: 36px;
}
.header-menu ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 43px;
  padding-left: 0;
}
.header-menu::after {
  content: "";
  width: 1px;
  height: 22px;
  background-color: rgba(0, 0, 0, 0.2);
  transform: translate(18px, 13px);
}

.header-phones {
  font-weight: 600;
  color: var(--font-black);
}

.header-search {
  cursor: pointer;
}

.main {
  margin: 0 auto;
  width: 100%;
}

.main-hero {
  width: 100%;
  height: 629px;
  background-image: url("../../assets/bg/impuls-bg.jpg");
  background-position: center center;
  background-size: cover;
  color: var(--font-dark);
}

.main-hero__headers {
  padding-top: 192px;
}

.main-hero__head1 {
  font-size: 64px;
  line-height: 150%;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 40px;
  margin-top: 0;
}

.main-hero__head2 {
  font-weight: 500;
  font-size: 36px;
  max-width: 484px;
  line-height: 150%;
}

.main-advantages {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 40px auto 25px;
  margin-top: 40px;
}

.advantages-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}

.advantages-item__img {
  width: 50px;
  height: 50px;
}